.member_container {
  width: 230px;
  height: 320px;
  border-radius: 5px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  position: absolute;
  top: 80px;
  right: 20px;
  z-index: 100;
}
.container_title {
  padding: 15px;
}
.title {
  font-size: 20px;
  text-align: center;
}
.close_btn {
  width: 20px;
  height: 20px;
  border-radius: 50px;
  border: none;
  background: #ec8385;
  cursor: pointer;
  position: absolute;
  top: 14px;
  right: 14px;
}
.close_btn:hover {
  background: #e66063;
}
.member_list {
  padding: 15px;
}
.member {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  margin-bottom: 8px;
  text-align: center;
}
.member:last-child {
  margin-bottom: 0px;
}
.crown {
  background-color: #e4e9ef;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  margin-right: 10px;
  img {
    margin: 5px;
    width: 20px;
    height: 20px;
  }
}

.list_nickname {
  width: 100px;
  overflow: hidden;
  margin-right: 10px;
}
.exile {
  cursor: pointer;
  right: 10px;
  width: 50px;
  background-color: #fa818b;
  color: white;
  border-radius: 5px;
}
